<template lang="pug">
.page-car-dealer
    my-page-title(title="车商销售列表")
    .bar
        el-form(:inline="true" @click.native.prevent="")
            el-form-item()
                el-button(type="primary" @click="back") 返回车商列表
    .content
        el-table(:data="list" border style="width: 100%")
            el-table-column(align="center" type="index")
            el-table-column(align="center" label="头像")
                template(slot-scope="scope")
                    my-avatar(:src="scope.row.avator")
            el-table-column(align="center" label="名称" prop="name")
            el-table-column(align="center" label="昵称" prop="nick")
            el-table-column(align="center" label="电话号码" prop="phone")
            el-table-column(align="center" label="性别" prop="sex")
            el-table-column(align="center" label="是否审核" prop="is_checked")
                template(slot-scope="scope") {{scope.row.is_checked==1?'是':'否'}}
            el-table-column(align="center" label="管理员" prop="is_manager")
                template(slot-scope="scope") {{scope.row.is_manager==1?'是':'否'}}
            el-table-column(align="center" label="操作")
                template(slot-scope="scope")
                    el-button(type="text" @click="deleteItem(scope.row)") 删除
    .pager
        el-pagination(
            layout="total,slot,prev,pager,next"
            :current-page.sync="pagination.curPage"
            :page-size="pagination.pageSize"
            :total="pagination.pageTotal"
            @current-change="pageCurPageChange"
            @size-change="pageSizeChange")
    //- my-car-dealer-editor(ref="my-car-dealer-editor")
</template>

<script>
// import CarDealerEditor from '../components/CarDealerEditor';
import pageMixin from '@/mixins/page';
export default {
    mixins: [pageMixin],
    data () {
        return {
            filter: {
                name: '',
                check: ''
            },
            list: []
        };
    },
    methods: {
        back () {
            history.back();
        },
        async deleteItem (item) {
            try {
                await this.$confirm('确认删除吗？');
                await this.$http.post('/admin/business/deleteuser', {
                    id: item.id
                });
                this.req();
            } catch (error) {
                console.log(error);
            }
        },
        async req () {
            try {
                let params = {
                    p: 1,
                    bid: this.$route.query.id
                };
                let { data } = await this.$http.post('/admin/business/userlist', params);
                console.log(data);
            } catch (error) {
                console.log(error);
            }
        }
    },
    created () {
        this.req();
    }
};
</script>

<style lang="scss" scoped>
.page-car-dealer {
    .bar {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
    .img {
        width: 40px;
        height: 40px;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
    }
}
</style>



